 la armonía y la disonancia.")]),_c('p',[_vm._v("Dos tipos de personajes en entornos diferentes, un personaje y un entorno ligados a la misma forma, ya sea circular, cuadrada o triangular crearán una sensación de "),_c('b',[_vm._v("armonía")]),_vm._v(" para el jugador. Esto es debido a que en ambos entornos se mantiene el mismo estilo, por ejemplo, un personaje redondo en un sitio lleno de nubes y paisajes redondos estaría en un espacio armonioso. Por otra parte, si los personajes están ligados a una forma diferente de la de su entorno se produce una sensación de "),_c('b',[_vm._v("disonancia")]),_vm._v(", debido a los diferentes temas emocionales de cada forma, es decir, un personaje de tipo triangular ligado a la agresión o poder, no encajaría muy bien en un mundo con elementos suaves, alegres, optimistas o viceversa, un personaje de tipo circular ligado a temas positivos no se “sentiría cómodo” en un ambiente o entorno hostil y amenazante ligado al triángulo. ")])]),_c('div',{staticClass:"col-lg-1"})])]),_vm._m(8),_c('p',{staticClass:"mb-4"},[_vm._v("Conocer el sistema del cuerpo anatómico permite estructurar de una forma eficaz los personajes, por lo que es importante entender estos conceptos básicos para su realización.")]),_c('p',{staticClass:"mb-5"},[_vm._v(" El cuerpo humano se divide en varias regiones que son la cabeza, el cuello, el tórax, miembros superiores, abdomen, pelvis y miembros inferiores. Estas son las regiones a tener en cuenta, pero para el dibujo básico se puede tener principalmente, la cabeza, el tórax y la cadera. A partir de ellos nacen los demás elementos que forman parte del cuerpo, a continuación, se podrá conocer la información referente a este proceso.")]),_c('SlyderB',{attrs:{"datos":_vm.datosSlyder}}),_c('Separador'),_vm._m(9),_c('p',[_vm._v("Se debe ayudar el trabajo del modelador, recreando poses de sus movimientos principales, para que a la hora de modelar y animar sea lo más cercano posible a como se tiene planeado. Puesto que es el personaje quien ayuda a desentrañar toda la historia y se debe dotar de las características coherentes para que se pueda desenvolver en todos los escenarios. Por ello, se hace necesario resaltar ciertas líneas de acción del personaje, líneas imaginarias que indican el movimiento del cuerpo de este, lo cual ayudará a transmitir las sensaciones y sentimientos del personaje en varias situaciones, tal como se muestra en la siguiente figura.")]),_vm._m(10),_c('p',{staticClass:"mb-5"},[_vm._v("Para trabajar el diseño de personajes, a continuación, se conocerá la línea de acción en un personaje llamado Flynn.")]),_vm._m(11),_vm._m(12),_c('p',{staticClass:"mb-4"},[_vm._v("Así como en las producciones en live action se acostumbra a realizar castings para seleccionar a los actores que mejor se adapten a los atributos físicos y psicológicos, desarrollados al momento de crear los personajes durante las etapas correspondientes al desarrollo de guion; en animación y videojuegos se lleva a cabo un proceso análogo denominado diseño de personajes, donde un equipo de artistas conceptuales se encargan de interpretar los personajes creados por los escritores para generar un casting que permita diseñar los atributos visuales que mejor se adapten a las características de cada uno.")]),_c('p',[_vm._v("Para realizar este proceso con éxito existe una serie de conocimientos indispensables y aspectos a tener en cuenta, los cuales serán descritos a continuación:")]),_c('TabsC',{staticClass:"color-acento-contenido mb-5"},[_c('div',{staticClass:"py-3 py-md-4",attrs:{"titulo":"Referencias y documentación"}},[_c('p',{staticClass:"mb-5"},[_vm._v("Para que un personaje funcione correctamente es indispensable tener en cuenta el contexto, la historia dentro de la cual se desarrolla y su propia historia individual que como ya se sabe, ha sido desarrollada desde la etapa del guion por parte de los escritores. Así como hacer un barrido sobre los orígenes del personaje, sus actitudes, aptitudes y motivaciones no solo los desarrollados en la etapa de escritura, sino los que puedan relacionarse con una época o un lugar específico. Por ejemplo, no es lo mismo diseñar un personaje que haya nacido en la década de 1950 en el continente latinoamericano, a uno que lo haya hecho en el siglo XXV en una colonia en Marte. Aunque en esta comparación en apariencia sea más fácil encontrar información sobre el primer caso, no quiere decir que para el segundo no sea relevante hacer una investigación sobre las condiciones conocidas del planeta y la misma historia y proyección de la raza humana, ya que esto permite obtener datos que brindan credibilidad al personaje.")]),_c('figure',{staticClass:"mb-5"},[_c('img',{attrs:{"src":require("@/assets/curso/tema1/img21.png")}})]),_c('p',{staticClass:"mb-4"},[_vm._v(" Por otro lado, la recolección de referencias visuales es una práctica indispensable al momento de diseñar, puesto que por mucha experiencia que se tenga, absolutamente nadie tiene almacenados en su cerebro todos los referentes que necesita. Por lo tanto, no está de más construir un banco visual que ayude a alimentar la creatividad y analizar el trabajo que realizaron otros artistas para tratar de entender de qué manera afrontan los retos presentados en sus respectivas producciones.")]),_c('p',{staticClass:"mb-5"},[_vm._v("Una buena manera de gestionar las referencias es mediante PureRef, una aplicación gratuita que permite crear tableros y superponerlos a otras aplicaciones para tenerlos siempre a la vista y que puede ser descargada en el siguiente enlace:")]),_c('div',{staticClass:"row mb-5"},[_c('div',{staticClass:"col-lg-3"}),_c('div',{staticClass:"col-lg-3"},[_c('a',{staticClass:"anexo mb-4 mb-lg-0",attrs:{"href":"https://www.pureref.com/","target":"_blank"}},[_c('div',{staticClass:"anexo__icono"},[_c('img',{attrs:{"src":require("@/assets/template/icono-link.svg")}})]),_c('div',{staticClass:"anexo__texto"},[_c('p',[_c('b',[_vm._v("Enlace web.")]),_vm._v(" PureRef ")])])