 desde la estructura ósea que soporta el cuerpo y muestra su relación con los músculos. No es necesario en principio memorizar el nombre de cada uno de los huesos y músculos que conforman el cuerpo, pero sí es sumamente importante saber dónde están ubicados, de dónde parten y a dónde llegan; debido que esto permite comprender su función en el movimiento y así mismo su importancia en la definición de siluetas y volúmenes del cuerpo.")])]),_c('div',{staticClass:"col-lg-1"})])]),_c('div',{staticClass:"py-3 py-md-4",attrs:{"titulo":"Proporciones del cuerpo humano"}},[_c('div',{staticClass:"row mb-5"},[_c('div',{staticClass:"col-lg-6"},[_c('figure',[_c('img',{attrs:{"src":require("@/assets/curso/tema1/img22.png")}})])]),_c('div',{staticClass:"col-lg-5"},[_c('p',{staticClass:"mb-4"},[_vm._v("Cada cuerpo posee características únicas que lo diferencian de los demás, sin embargo, desde la antigüedad han sido desarrollados diferentes sistemas de proporción para representar la figura humana de una manera armónica buscando unas proporciones ideales, los egipcios por ejemplo usaban la medida del puño como base para determinar las demás proporciones del cuerpo, determinando que la altura de este correspondía a 18 veces el tamaño del puño usando 2 para el rostro, diez en la distancia que va de los hombros a las rodillas y otros 6 para la distancia comprendida entre las rodillas y los pies.")]),_c('p',[_vm._v("El sistema más común es el que usa la dimensión de la cabeza como medida de referencia, con el cual muchos artistas han usado en sus obras proporciones basadas en sistemas de 7, 7 ½, 8, 8 1⁄2 y hasta 9 cabezas para determinar la estatura de la figura humana.")])])]),_c('p',{staticClass:"mb-4"},[_vm._v(" Estos sistemas de medida y proporción llevan por nombre canon (del griego Χανων, que significa regla). Uno de los cánones más conocidos es el del Hombre de Vitruvio, famoso dibujo de Leonardo da Vinci realizado alrededor de 1490 en el cual se representa una figura masculina inscrita al interior de un cuadrado y una circunferencia con 2 pares de brazos y piernas sobrepuestas, este estudio anatómico fue hecho a partir de textos arquitectónicos de Marco Vitruvio (de donde saca su nombre) y se rige bajo un sistema de proporciones de 8 cabezas.")]),_c('p',[_vm._v("Cabe resaltar que aunque se acepte el uso de un canon anatómico para la representación de las proporciones perfectas, esta es una idealización y las proporciones reales de cada persona varían en función de diversos factores como su origen étnico, genética heredada, condiciones de vida, etc. sin embargo, los cánones son útiles para construir estructuras anatómicas sólidas y su conocimiento nos permite tomar ciertas licencias para poder deformar las proporciones y así generar personajes con estilos más realistas o cartoon.")])])]),_c('h2',[_vm._v("Personajes no jugadores NPC")]),_c('p',[_vm._v("En el diseño de personajes se encuentran los personajes no jugadores NPC, estos hacen referencia a los que habitan los escenarios del juego, pero el usuario no puede controlarlos, los cuales se pueden clasificar en: ")]),_vm._m(13),_c('h2',[_vm._v("Limitantes")]),_c('p',{staticClass:"mb-5"},[_vm._v("En los videojuegos a diferencia de la animación 3D el renderizado es en tiempo real, por ende, se debe manejar una limitante en cuanto a polígonos y texturas para que todo fluya con movimientos mínimos de 24 cuadros por segundo. Todo esto depende de la plataforma en la cual se está pensado lanzar el juego, debido a las limitantes de cada hardware, pues consumirán recursos de la memoria del dispositivo, un ejemplo de ello son los celulares, que al ser de menor potencia, deben utilizar menor número de elementos en el juego. Dicho lo anterior, lo que se debe tener en cuenta a la hora de elegir la plataforma en la que se lanzará el juego es:")]),_vm._m(14),_vm._m(15),_vm._m(16),_vm._m(17),_c('Separador'),_vm._m(18),_c('p',[_vm._v("Los props son aquellos objetos y enseres que aparecen en un escenario a m